import React, { useState, useEffect } from 'react'
import { io } from "socket.io-client";
import { Card, Form, Button } from 'react-bootstrap'
var socket;

const MessageModal = ({ list, pm }) => {
    const [message, setMessage] = useState('');
    const [messages, setMessages] = useState([]);
    const [receiveMessage, setReceiveMessage] = useState('')


    const ENDPOINT = "http://localhost:4000"

    useEffect(() => {
        socket = io.connect(ENDPOINT);

    }, [ENDPOINT]);

    useEffect(() => {
        socket.on('private_receive', (privateData) => {
            setReceiveMessage(privateData.messageModal)
        })
    }, [])

    useEffect(() => {
        socket.on("private_receive", (message) => {
            setMessages(messages => [...messages, message.message])
        })
    }, [])

    const handleSubmit = (e) => {
        // socket = io.connect(ENDPOINT);
        e.preventDefault();
        socket.emit('private', {
            to: list._id,
            message: message
        });
    }

    return (
        <div>
            <Card border="secondary" style={{ width: '30rem' }} className="p-3">
                <Card.Header>{list.fullname}</Card.Header>
                <Card.Body>
                    {pm}
                </Card.Body>

                <Form onSubmit={handleSubmit}>
                    <Form.Group controlId="formBasicEmail">
                        <input
                            type="text" placeholder="id" value={list._id} readOnly />
                        <Form.Control type="text" placeholder="Message.." value={message} onChange={(e) => setMessage(e.target.value)} />

                    </Form.Group>
                    <Button variant="primary" type="submit">
                        Submit
  </Button>


                </Form>
            </Card>

        </div>
    )
}

export default MessageModal
